import axios from 'axios'
import { Toast, MessageBox } from 'mint-ui'
import store from '@/store'
import { getToken_H5, getToken_360App } from '@/utils/auth'

// create an axios instance
const service = axios.create({
  withCredentials: true,
  baseURL: process.env.BASE_API,
  timeout: 5000
})

// request interceptor
service.interceptors.request.use(
  config => {
    if (store.getters.token) {
      // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
      config.headers['token'] = getToken_H5()
    }
    // 根据情况动态设置baseurl
    if (config.is360AppAPI === true) {
      config.baseURL = process.env.BASE_API_APP
      config.headers['token'] = getToken_360App()
    }

    // console.log('config === \n' + JSON.stringify(config))
    return config
  },
  error => {
    // Do something with request error
    console.log('[error] ' + error) // for debug
    return Promise.reject(error)
  }
)

// respone interceptor
service.interceptors.response.use(
  // response => response,
  /**
   * 下面的注释为通过在response里，自定义code来标示请求状态
   * 当code返回如下情况则说明权限有问题，登出并返回到登录页
   * 如想通过xmlhttprequest来状态码标识 逻辑可写在下面error中
   * 以下代码均为样例，请结合自生需求加以修改，若不需要，则可删除
   */
  response => {
    return response

    /* 返回数据统一处理
    const res = response.data
    if (res.code !== 20000) {
      Toast({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })
      // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
      if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
        // 请自行在引入 MessageBox
        // import { Message, MessageBox } from 'element-ui'
        MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
          confirmButtonText: '重新登录',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload() // 为了重新实例化vue-router对象 避免bug
          })
        })
      }
      return Promise.reject(res.message)
    } else {
      return response.data
    }
    */
  },
  error => {
    console.log('[error] ' + error) // for debug
    Toast({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



